:root {
    --primary : #fff;
}

.btn {
    padding: 8px 20px;
    border-radius: 2px;
    outline: none;
    border: none;
    cursor: pointer;
}

.btn--primary {
    background-color: var(--primary);
    color: rgb(0, 0, 0);
    border: 1px solid var(--primary);
}

.btn--outline {
    background-color: transparent;
    color: var(--primary);
    padding: 8px 20px;
    border: 1px solid var(--primary);
    transition: all 0.2s ease-out;
}

.btn--medium {
    padding: 8px 20px;
    font-size: 20px;
}

.btn--large {
    padding: 16px 26px;
    font-size: 20px;
}

.btn--medium:hover, .btn--large:hover {
    background-color: var(--primary);
    color: rgb(0, 0, 0);
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
}